import styled from 'styled-components'
import React from 'react'

export const Announcement = () => {
    return (
        <Container>
            Super Deal! Free Shipping on Order Over $50
        </Container>
    )
}

const Container = styled.div`
    height: 30px;
    background-color: teal;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    font-weight: 500;
`