import React from 'react'
import styled from "styled-components"
import bg from '../images/bg.jpg'

export const Register = () => {
    return (
        <Container>
            <Wrapper>
                <Title>CREATE AN ACCOUNT</Title>
                <Form>
                    <Input type="text" placeholder="name" name="name"/>
                    <Input type="text" placeholder="last name" name="lastName"/>
                    <Input type="text" placeholder="username" name="username"/>
                    <Input type="email" placeholder="email" name="email"/>
                    <Input type="password" placeholder="passowrd" name="password"/>
                    <Input type="password" placeholder="confirm password" name="confirmPassword"/>
                    <Agreement>
                        By creating an account, I consent to the processing of my personal data in accordance with <b>PRIVACY POLICY</b>
                    </Agreement>
                    <Button>CREATE</Button>
                </Form>
            </Wrapper>
        </Container>
    )
}


const Container = styled.div`
    width: 100%;
    height: 100vh;
    background: linear-gradient(rgba(255,255,255,0.6), rgba(255,255,255,0.6)), url(${bg}) center/cover;
    display: flex;
    justify-content: center;
    align-items: center;
`
const Wrapper = styled.div`
    padding: 20px;
    width: 40%;
    background-color: white;
    @media screen and (max-width: 600px) {
        width: 80%;
    }
`
const Title = styled.h1`
    font-size: 1.4rem;
    font-weight: 300;
`
const Form = styled.form`
    display: flex;
    flex-wrap: wrap;
`
const Input = styled.input`
    flex: 1;
    min-width: 40%;
    margin: 20px 10px 0 0;
    padding: 10px;
`
const Agreement = styled.div`
    font-size: 0.8rem;
    margin: 20px 0;
`
const Button = styled.button`
    width: 40%;
    border: none;
    padding: 15px 20px;
    background-color: teal;
    color: white;
    cursor: pointer;
`