import React from 'react'
import styled from 'styled-components'
import { mobile } from './@media'

export const CatItem = ({item}) => {
    return (
        <Container>
            <Image src={item.img} />
            <Info>
                <Title>{item.title}</Title>
                <Button>SHOP NOW</Button>
            </Info>
        </Container>
    )
}

const Container = styled.div`
    flex: 1;
    margin: 3px;
    height: 70vh;
    position: relative;
    overflow: hidden;
    &:hover >img {
        transform: scale(1.1);
    }
`
const Image = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all .5s ease;
    ${mobile({height: "30vh"})};
    object-position: top 20% left 50%;
`
const Info = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
   
`
const Title = styled.h1`
    color: white;
    margin-bottom: 20px;
    font-size: clamp(1.5rem, 3vw, 3rem);
`
const Button = styled.button`
    border: none;
    padding: 10px;
    outline: none;
    background-color: white;
    color: grey;
    cursor: pointer;
    font-weight: 600;
`