import React, { useState } from 'react'
import {ArrowLeft} from '@styled-icons/material/ArrowLeft'
import {ArrowRight} from '@styled-icons/material/ArrowRight'
import styled from "styled-components"
import {SliderItems} from "../data"
import {  tablet } from './@media'
import {Link} from 'react-router-dom'



export const Slider = () => {
    const [slide, setSlide] = useState(0)
    
    const handleClick = (dir) => {
        if (dir === "left") {
            setSlide(slide > 0 ? slide - 1 : 2)
        } else {
            setSlide(slide < 2 ? slide + 1 : 0)
        }
    }
    
    return (
        <Container>
            <Arrow onClick={() => handleClick("left")}>
                <ArrowLeft/>
            </Arrow>
            <Wrapper slide={slide}>
                {SliderItems.map((item, i) => (
                    <Slide bg={item.bg} key={item.id}>
                        <ImgContainer>
                            <Image src={item.img} alt="by Ahmed Satti from Pexels"/>
                        </ImgContainer>
                        <InfoContainer>
                            <Title>{item.title}</Title>
                            <Description>{item.description}</Description>
                            <Link to="product-list"><Button>SHOP NOW</Button></Link>
                        </InfoContainer>
                    </Slide>
                ))}
            </Wrapper>
            <Arrow onClick={() => handleClick("right")}>
                <ArrowRight/>
            </Arrow>
        </Container>
    )
}

const Container = styled.div`
    display: flex;
    background-color: coral;
    position: relative;
    overflow: hidden;
`
const Arrow = styled.div`
    width: 50px;
    height: 50px;
    background-color: #fff7f7;
    border-radius: 50%;
    display: grid;
    place-content: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    opacity: 0.5;
    transition: opacity .3s ease;
    z-index: 2;
    &:hover {
        opacity: 1;
    }
    &:first-child {
        left: 10px;
    }
    &:last-child {
        right: 10px;
    }
    >svg {
        pointer-events: none;
    }
`
const Wrapper = styled.div`
    height: 100%;
    display: flex;
    transform: translateX(${props => props.slide * -100}vw);
    transition: transform 1s ease-in-out;
`
const Slide = styled.div`
    display: flex;
    align-items: center;
    height: calc(100vh - 80px);
    width: 100vw;
    background-color: #${props => props.bg};
    ${tablet({flexDirection: "column-reverse"})};
`
const ImgContainer = styled.div`
    flex: 1;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    ${tablet({height: "50%"})};
`
const Image = styled.img`
    height: 80%;
    -webkit-tap-highlight-color: transparent;
    ${tablet({height: "120%"})};
`
const InfoContainer = styled.div`
    flex: 1;
    padding: 50px;
`
const Title = styled.h1`
    font-size: clamp(1.5rem, 3vw, 4rem);
`
const Description = styled.p`
    margin: 50px 0;
    font-size: 1.2rem;
    font-weight: 500;
    letter-spacing: 3px;
`
const Button = styled.button`
    padding: 10px;
    font-size: 1.2rem;
    background-color: transparent;
    cursor: pointer;
`