import { Add, Remove } from '@styled-icons/material-outlined'
import { useEffect, useState } from 'react'
import { useLocation } from 'react-router'
import styled from "styled-components"
import {mobile} from '../components/@media'


export const Product = () => {
    const [prod, setProd] = useState([])
    const location = useLocation()
    const id = decodeURI(location.pathname.substr(9));
    

    useEffect(() => {
        const url = "https://fakestoreapi.com/products"
       
        const fetchData = async () => {
            try {
                const res = await fetch(url)
                const json = await res.json()
                const indexItem = json.findIndex(x => x.title === id);
                const product = json[indexItem];
                setProd(product)
            } catch (error) {
                alert(error)
            }
        }
        fetchData()
    }, [])

    
    return (
        <Container>
            {prod &&
                <Wrapper>
                    <ImgContainer>
                        <Image src={prod?.image} alt="sdfsd"/>
                    </ImgContainer>
                    <InfoContainer>
                        <Title>{prod?.title}</Title>
                        <Desc>{prod?.description}</Desc>
                        <Price>$ {prod?.price}</Price>
                        <FilterContainer>
                            <Filter>
                                <FilterTitle>Color</FilterTitle>
                                <FilterColor color="black"/>
                                <FilterColor color="darkblue"/>
                                <FilterColor color="grey"/>
                            </Filter>
                            <Filter>
                                <FilterTitle>Size</FilterTitle>
                                <FilterSize defaultValue>
                                    <FilterSizeOption>XS</FilterSizeOption>
                                    <FilterSizeOption>S</FilterSizeOption>
                                    <FilterSizeOption>M</FilterSizeOption>
                                    <FilterSizeOption>L</FilterSizeOption>
                                    <FilterSizeOption>XL</FilterSizeOption>
                                </FilterSize>
                            </Filter>
                        </FilterContainer>
                        <AddContainer>
                            <AmountContainer>
                                <Remove/>
                                <Amount>1</Amount>
                                <Add/>
                            </AmountContainer>
                            <Button>Add to cart</Button>
                        </AddContainer>
                    </InfoContainer>
                </Wrapper>
}
        </Container>
    )
}

const Container = styled.div`

`
const Wrapper = styled.div`
    padding: 50px;
    display: flex;
    ${mobile({padding: "10px", flexDirection: "column"})};
`
const ImgContainer = styled.div`
    flex: 1;
`
const Image = styled.img`
    width: 100%;
    height: 50vh;
    object-fit: contain;
    ${mobile({height: "40vh"})};
`
const InfoContainer = styled.div`
    flex: 1;
    padding: 0px 50px;
    ${mobile({padding: 10})};
`
const Title = styled.h1`
    font-weight: 200;
`
const Desc = styled.p`
    margin: 20px 0;
`
const Price = styled.span`
    font-weight: 100;
    font-size: 40px;
`
const FilterContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: 30px 0;
    ${mobile({width: "100%"})};
`
const Filter = styled.div`
    display: flex;
    align-items: center;
`
const FilterTitle = styled.span`
    font-size: 1.2rem;
    font-weight: 200;
`
const FilterColor = styled.div`
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: ${props => props.color};
    margin: 0 5px;
    cursor: pointer;
`
const FilterSize = styled.select`
    margin-left: 10px;
    padding: 5px;
`
const FilterSizeOption = styled.option`

`
const AddContainer = styled.div`
    display: flex;
    align-items: center;
    width: 50%;
    justify-content: space-between;
    ${mobile({width: "100%"})};
`
const AmountContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    >svg {
        cursor: pointer;
    }
`
const Amount = styled.span`
    width: 30px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid teal;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 5px;
`
const Button = styled.button`
    padding: 15px;
    border: 2px solid teal;
    background-color: white;
    cursor: pointer;
    font-weight: 700;
    &:hover {
        background-color:  #f8f4f4;
    }
`