import { Facebook, Instagram, Twitter, Reddit, Phone } from '@styled-icons/bootstrap'
import { Address, Email } from '@styled-icons/entypo'
import {Pinterest} from '@styled-icons/icomoon/Pinterest'
import React from 'react'
import styled from 'styled-components'


export const Footer = () => {
    return (
        <Container>
            <Left>
                <Logo>LAMA.</Logo>
                <Desc>There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humor, or randomised words which don't look even slightly believable.</Desc>
                <SocialContainer>
                    <SocialIcon color="3B5999">
                        <Facebook/>
                    </SocialIcon>
                    <SocialIcon color="E4405F">
                        <Instagram/>
                    </SocialIcon>
                    <SocialIcon color="55ACEE">
                        <Twitter />
                    </SocialIcon >
                    <SocialIcon color="E60023">
                        <Pinterest/>
                    </SocialIcon>
                    <SocialIcon color="FF1100">
                        <Reddit/>
                    </SocialIcon>
                </SocialContainer>
            </Left>
            <Center>
                <Title>Useful Links</Title>
                <List>
                    <Li>Home</Li>
                    <Li>Cart</Li>
                    <Li>Man Fashion</Li>
                    <Li>Woman Fashion</Li>
                    <Li>Accessories</Li>
                    <Li>My Account</Li>
                    <Li>Order Tracking</Li>
                    <Li>Wishlist</Li>
                    <Li>Terms</Li>
                </List>
            </Center>
            <Right>
                <Title>Contact</Title>
                <ContactItem>
                    <Address/>
                    32C-34A, Saint Row Blvd 3500 - A, Mowee Tehy
                </ContactItem>
                <ContactItem>
                    <Phone/>
                    -345 787 989 990
                </ContactItem>
                <ContactItem>
                    <Email/>
                    contact@support.email.com
                </ContactItem>
                <Payment src="https://repasoasvab.com/wp-content/uploads/2018/06/credit-cards-logos.png" alt="payment" />
            </Right>
        </Container>
    )
}

const Container = styled.footer`
    display: flex;
    @media screen and (max-width: 900px) {
        flex-direction: column;
    }
`
const Left = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;
`
const Logo = styled.h1`

`
const Desc = styled.p`
    margin: 20px 0;
`
const SocialContainer = styled.div`
    display: flex;
`
const SocialIcon = styled.div`
    width: 40px;
    height: 40px;
    color: #${props => props.color};
    >svg {
        cursor: pointer;
    }
`
const Center = styled.div`
    flex: 1;
    padding: 20px;
`
const Title = styled.h3`
    margin-bottom: 30px;
`
const List = styled.ul`
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
`
const Li = styled.li`
    width: 50%;
    margin-bottom: 10px;
`
const Right = styled.div`
    flex: 1;
    padding: 20px;
`
const ContactItem = styled.div`
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    >svg {
        margin-right: 10px;
    }
`
const Payment = styled.img`
    height: 50px;
`